       .pricingTable{
    text-align: center;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.pricingTable .pricingTable-header{
    padding: 30px 0;
    background: #4d4d4d;
    position: relative;
    transition: all 0.3s ease 0s;
    position: sticky;
    top: 0;
    z-index: 8;
}

.pricingTable:hover .pricingTable-header{
    background: #dc3545;
}

.pricingTable .pricingTable-header:before{
    position: sticky;
    top: 0;
    z-index: 2;
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid #d9d9d8;
    bottom: 12px;
}


.pricingTable .pricingTable-header:before,
.pricingTable .pricingTable-header:after{
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid #d9d9d8;
    position: absolute;
    bottom: 12px;
    display: none;
}

.pricingTable .pricingTable-header:before{
    left: 40px;
    display: none;
}

.pricingTable .pricingTable-header:after{
    right: 40px;
    display: none;
}

.pricingTable .heading{
    font-size: 20px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 0;
}

.pricingTable .price-value{
    display: inline-block;
    position: relative;
    font-size: 30px;
    font-weight: bold;
    color: #dc3545;
    transition: all 0.3s ease 0s;
}

.pricingTable:hover .price-value{
    color: #fff;
}

.pricingTable .currency{
    font-size: 30px;
    font-weight: bold;
    position: absolute;
    top: 6px;
    left: -19px;
}

.pricingTable .month{
    font-size: 16px;
    color: #fff;
    position: absolute;
    bottom: 15px;
    right: -45px;
    text-transform: uppercase;
}

.pricingTable .pricing-content{
    padding-top: 20px;
    background: #fff;
    position: relative;
  
}

.pricingTable .pricing-content:before,
.pricingTable .pricing-content:after{
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid #7c7c7c;
    position: absolute;
    top: 12px;
   
}

.pricingTable .pricing-content:before{
    left: 40px;
    display: none;
}

.pricingTable .pricing-content:after{
    right: 40px;
    display: none;
}

.pricingTable .pricing-content ul{
    padding: 0 10px;
    margin: 0;
    list-style: none;
}

.pricingTable .pricing-content ul:before,
.pricingTable .pricing-content ul:after{
    content: "";
    width: 8px;
    height: 46px;
    border-radius: 3px;
    background: linear-gradient(to bottom,#818282 50%,#727373 50%);
    position: absolute;
    top: -22px;
    z-index: 2;
    box-shadow: 0 0 5px #707070;
    transition: all 0.3s ease 0s;
    display: none;
}
/*.pricingTable .vishal:before{
   position: sticky;
    top: 0;
}*/

.pricingTable:hover .pricing-content ul:before,
.pricingTable:hover .pricing-content ul:after{
    background: linear-gradient(to bottom, #dc3545 50%, #dc3545 50%);
}

.pricingTable .pricing-content ul:before{
    left: 44px;
    display: none;
}

.pricingTable .pricing-content ul:after{
    right: 44px;
    display: none;
}

.pricingTable .pricing-content ul li{
    font-size: 15px;
    font-weight: bold;
    color: #777473;
    padding: 10px 0;
    border-bottom: 1px solid #d9d9d8;
}

.pricingTable .pricing-content ul li:last-child{
    border-bottom: none;
}

.pricingTable .read{
    display: inline-block;
    font-size: 16px;
    color: #fff;
    text-transform: uppercase;
    background: #dc3545;
    padding: 8px 25px;
    margin: 30px 0;
    transition: all 0.3s ease 0s;
}

.pricingTable .read:hover{
    text-decoration: none;
}

.pricingTable:hover .read{
    background: #dc3545;
}

.book-publiser-link{
    color: #da474a;
    font-weight: 800;
}

@media screen and (max-width: 990px){
    .pricingTable{ margin-bottom: 25px; }
}


